input[type="text" i] {
  color: #727272;
  font-family: aktiv-grotesk, sans-serif;
  font-size: .8vw;
  font-weight: 600;
  line-height: 1vw;
}

input[type="date"]:focus {
  text-transform: uppercase;
  color: #727272;
  border: 0;
  outline: none;
  font-family: aktiv-grotesk, sans-serif;
  font-size: .7vw;
  font-weight: 600;
  line-height: .9vw;
}

.co-buyer__wrapper {
  grid-column-gap: .5vw;
  grid-row-gap: .5vw;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  display: flex;
}

.co-buyer-employment-status__wrapper {
  width: 100%;
}

.car-image__wrapper {
  z-index: 999;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.car-image__wrapper img {
  object-fit: cover;
  width: 32vw;
  height: 18vw;
}

